import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
// minified version is also included
// import 'react-toastify/dist/ReactToastify.min.css';
import options from './options';
import styles from './Form.module.css';
import { ReactComponent as Search } from './search.svg';

const Form = ({ fetchFilms, query, setFilmQuery, setActivePage }) => {
  const [value, setValue] = useState(query);
  const [showAutosuggest, setShowAutosuggest] = useState(false);
  const [suggestions, filterSuggestions] = useState(options);
  useEffect(() => {
    setActivePage(1);
    // eslint-disable-next-line
  }, [query]);
  const handleValueChange = e => {
    setValue(e.target.value);
    const filteredSuggestions = options.filter(option => {
      const lowerCaseSuggestions = option.label.toLowerCase();
      return lowerCaseSuggestions.includes(e.target.value.toLowerCase());
    });
    filterSuggestions(filteredSuggestions);
  };
  const handleSubmit = e => {
    e.preventDefault();
    if (value === '') {
      toast('Nothing was entered!');
      return;
    }
    setFilmQuery(value);
    fetchFilms(value, 1);
  };
  const handleFormClick = () => {
    if (!value) {
      setShowAutosuggest(true);
    }
  };
  const handleAutocompleteClick = e => {
    e.preventDefault();
    setValue(e.target.textContent);
    setShowAutosuggest(false);
    setFilmQuery(e.target.textContent);
    fetchFilms(e.target.textContent, 1);
  };

  return (
    <div>
      <h2 className={styles.title}> Home </h2>

      <form
        onSubmit={handleSubmit}
        className={styles.form}
        onClick={handleFormClick}
      >
        <input
          type="text"
          onChange={handleValueChange}
          value={value}
          placeholder="Enter film title here..."
          className={styles.input}
        />

        <button type="submit" className={styles.button}>
          <Search className={styles.search} />
        </button>
      </form>

      {showAutosuggest && suggestions.length > 0 && (
        <div className={styles.autosuggest}>
          <h3 className={styles.autosuggestTitle}> Popular films: </h3>

          <ul className={styles.autosuggestList}>
            {suggestions.map(suggest => (
              <li className={styles.autosuggestItem} key={suggest.label}>
                <button
                  className={styles.autosuggestButton}
                  type="button"
                  onClick={handleAutocompleteClick}
                >
                  {suggest.label}
                </button>
              </li>
            ))}
          </ul>
        </div>
      )}

      <ToastContainer />
    </div>
  );
};
Form.propTypes = {
  fetchFilms: PropTypes.func.isRequired,
  query: PropTypes.string.isRequired,
  setFilmQuery: PropTypes.func.isRequired,
  setActivePage: PropTypes.func.isRequired,
};
export default Form;
